@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:400,600,700&display=swap');

body{
  height: 100%;
  width: 100%;
  font-family: 'Josefin Sans', sans-serif;
  color: grey;
}

.navbar-brand img{
  width: 110px;
}

.nav-item{
  padding-right: 20px;
}

.navbar-nav li{
  font-size: 1.1rem !important;
}

.carousel-inner{
  height: 600px;
}
.carousel-item img {
  width: 100%;
  height: auto;
}

/* Text P */



.carousel-caption{
  position: absolute;
  top: 50%;
  transform: translateY(-80%);
  text-shadow: 1px 1px 10px black;
}

/* .carousel-indicators{
  position: absolute;
  top: 75%;
} */

@media (max-width:810px){
  .carousel-caption .display-2{
    font-size: 3em;
  }
  
  .carousel-inner{
    height: auto;
  }
  .lead{
    font-size: 3vw;
  }

}

@media (max-width:360px){
   
  .carousel-caption{
    position: absolute;
    top: 40%;
  }
  .carousel-caption .display-2 h3{
    font-size: 10px;
  }
  .btn{
    font-size: 7px;
  }
}

.jumbotron{
  padding: 1em;
  border-radius: 0;
  width: 70%;
  padding: 20px;
  margin: 0 auto; 
}

.padding{
  padding-bottom: 2em;
}

.alert{
  width: 75%;
  margin: 0 auto;
  padding-top: 2em;
}
.alert hr{
  border-top: 2px solid black;
  width: 95%;
  margin-top: 0.3em;
  margin-bottom: 1em;
}

.fa-html5{color: orange}
.fa-bold{color: #9900ff}
.fa-css3{color: #1a75ff}
.fa-react{color: blue}
.fa-js{color: yellow}

.fa-html5, .fa-bold, .fa-css3 , .fa-react , .fa-js{
    font-size: 4em;
    margin : 1em;
}

.img-fluid{
  border-radius: 20%;
}

#fixed{
  height: 400px;
  border-top: 4px solid #f0f0f0;
  border-bottom: 4px solid #f0f0f0;
  background: url('img/honeyBg.jpg') no-repeat center center fixed ;
  background-blend-mode: multiply;
  background-color: lightblue;
  background-size: cover;
  -o-background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size : cover;
}

#gifsBtn{
  margin-bottom: 20px;
}
#gifs img{
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
}